.navbar {
    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .menu-bars{
    margin-left: 2rem;
    font-size: 2rem;
    color: aliceblue;
    position: fixed;
  }
  
  .nav-menu{
      background-color: #060b26;
      width: 250px;
      height: 100vh;
      display: flex;
      justify-content: center;
      position: fixed;
      color: aliceblue;
      left: -100%;
      transition: 1000ms;
      z-index: 55;
  }

  .nav-menu.active{
    left: 0;
    transition: 350ms;
  }

  .nav-text{
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 8px 0px 8px 16px;
      list-style: none;
      height: 60px;
      font-size: 18px;
      text-decoration: none;
      color: aliceblue;
  }

  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;

  }

  .nav-text:hover {
    background-color: #1a83ff;
    border-radius: 4px;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  span {
      margin-left: 1rem;
  }